<template>
  <div class="secure">
    <h1>Hello Admin!</h1>
    <h3 class="h3-text">Orders</h3>
    <!--    <img src="../assets/shopping-list.png" alt="">-->
    <!--    <img src="../assets/hourglass.png" alt="">-->
    <!--    <img src="../assets/check.png" alt="">-->
    <table class="white-bg">
      <thead>
        <tr>
          <th class="text-center">Email</th>
          <th class="text-left">Status</th>
          <th class="text-left">Type</th>
          <th class="text-left">Date</th>
        </tr>
      </thead>
      <tbody class="rows-table" v-for="item in orders" :key="item.email">
        <tr @click="item.displayItem = !item.displayItem">
          <td class="text-center">{{ item.email }}</td>
          <td>{{ item.status }}</td>
          <td>{{ item.type }}</td>
          <td>{{ item.date }}</td>
        </tr>
        <tr v-if="item.displayItem" class="text-center hide-container">
          <td
            v-if="item.photos"
            style="display: grid; grid-template-columns: 1fr 1fr"
          >
            <div v-for="(photo, index) in item.photos" :key="index">
              <a v-bind:href="photo">Download Photo {{ index + 1 }}</a>
            </div>
          </td>
          <td>
            <form @submit.prevent>
              <input
                @change="onFileChange(item)"
                ref="files"
                type="file"
                name="filefield"
                multiple
                required
              />
              <br /><br />
              <input
                @click="send(item)"
                type="submit"
                value="Send files to user"
                class="submit-btn"
              />
            </form>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { storage, store } from "../db.js";
//
export default {
  name: "SecureView",
  props: ["auth"],
  data() {
    return {
      authenticated: localStorage.getItem("authenticated"),
      orders: [],
      imagesUrl: [],
      images: [],
    };
  },
  mounted() {
    if (!this.authenticated) {
      this.$router.push({ path: "/" });
    }
    store
      .collection("orders")
      .get()
      .then((querySnapshot) => {
        querySnapshot.docs.map((doc) =>
          this.orders.push({
            ...doc.data(),
            id: doc.id,
            date: this.timeSince(new Date(doc.data().date)),
            displayItem: false,
          })
        );
      });
  },
  methods: {
    postmail(item) {
      var myHeaders = new Headers();
      myHeaders.append(
        "Authorization",
        "Basic YXBpOjg4NWIwMTJmM2Q3MDllZjNmZjI5MTU2MDIyMzE1NmE5LTQ4YzA5MmJhLWY0YzlmOTBh"
      );
      console.log(item.email);
      var formdata = new FormData();
      formdata.append(
        "from",
        "Mailgun Sandbox <postmaster@sandbox993e747b50614293bf05bc935282c7d0.mailgun.org>"
      );
      formdata.append("to", `Alexander Shelofastov <shelofasstov@gmail.com>`);
      formdata.append("subject", "Hello");
      formdata.append("template", "send_images");
      formdata.append("h:X-Mailgun-Variables", '{"test": "test"}');

      this.images.forEach((image) =>
        formdata.append("attachment", image, image.name)
      );

      var requestOptions = {
        method: "POST",
        headers: myHeaders,
        body: formdata,
        redirect: "follow",
      };

      fetch(
        "https://api.mailgun.net/v3/sandbox993e747b50614293bf05bc935282c7d0.mailgun.org/messages",
        requestOptions
      )
        .then((response) => response.text())
        .then((result) => console.log(result))
        .catch((error) => console.log("error", error));
    },
    send(item) {
      this.postmail(item);

      store
        .collection("orders")
        .doc(item.id)
        .update({ status: "done" })
        .then(() => {
          console.log("user updated!");
        });
      item.status = "done";
    },
    onUpload(image, item) {
      const storageRef = storage
        .ref(`ordersDone/${item.email}/${image.name}`)
        .put(image);
      storageRef.on(
        `state_changed`,
        (snapshot) => {
          this.uploadValue =
            (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        },
        (error) => {
          console.log(error.message);
        },
        () => {
          this.uploadValue = 100;
          storageRef.snapshot.ref.getDownloadURL().then((url) => {
            this.imagesUrl.push(url);
          });
        }
      );
    },
    onFileChange(item) {
      const files = this.$refs.files[0].files;
      if (!files.length) return;
      this.images = Array.from(files);

      this.images.forEach((image) => this.onUpload(image, item));
    },
    timeSince(date) {
      var seconds = Math.floor((new Date() - date) / 1000);

      var interval = seconds / 31536000;

      if (interval > 1) {
        return Math.floor(interval) + " years";
      }
      interval = seconds / 2592000;
      if (interval > 1) {
        return Math.floor(interval) + " months";
      }
      interval = seconds / 86400;
      if (interval > 1) {
        return Math.floor(interval) + " days";
      }
      interval = seconds / 3600;
      if (interval > 1) {
        return Math.floor(interval) + " hours";
      }
      interval = seconds / 60;
      if (interval > 1) {
        return Math.floor(interval) + " minutes";
      }
      return Math.floor(seconds) + " seconds";
    },
  },
};
</script>
