<template>
  <div class="flex-container form" @submit.prevent>
    <div class="content-container">
      <div class="form-container">
        <form v-if="formEmpty" ref="form" @submit.prevent="create">
          <h1>Fill your contact info here and upload files</h1>
          <br />
          <br />
          <span class="subtitle">Email:</span>
          <br />
          <input v-model="email" type="email" name="email" id="" required />
          <br />
          <span class="subtitle">Files:</span>
          <br />
          <input
            @change="onFileChange"
            type="file"
            name="filefield"
            multiple
            required
          />
          <br /><br />
          <input type="submit" value="Send us files" class="submit-btn" />
        </form>
        <h1 v-else>
          Thanks! we will send you avatars to {{ email }} in 2 hours
          <br />
        </h1>
      </div>
      <p>
        if something went wrong, contact us:
        <br />
        email.com
      </p>
    </div>
  </div>
</template>

<script>
import { store, storage } from "../db.js";

// console.log(db);
export default {
  name: "SuccessComponent",
  data() {
    return {
      email: "",
      images: [],
      imagesUrl: [],
      formEmpty: true,
    };
  },
  methods: {
    postmail() {
      var myHeaders = new Headers();
      myHeaders.append(
        "Authorization",
        "Basic YXBpOjg4NWIwMTJmM2Q3MDllZjNmZjI5MTU2MDIyMzE1NmE5LTQ4YzA5MmJhLWY0YzlmOTBh"
      );

      var formdata = new FormData();
      formdata.append(
        "from",
        "Mailgun Sandbox <postmaster@sandbox993e747b50614293bf05bc935282c7d0.mailgun.org>"
      );
      formdata.append("to", "Alexander Shelofastov <shelofasstov@gmail.com>");
      formdata.append("subject", "Hello Alexander Shelofastov");
      formdata.append("template", "new_order");
      formdata.append("h:X-Mailgun-Variables", '{"test": "test"}');

      var requestOptions = {
        method: "POST",
        headers: myHeaders,
        body: formdata,
        redirect: "follow",
      };

      fetch(
        "https://api.mailgun.net/v3/sandbox993e747b50614293bf05bc935282c7d0.mailgun.org/messages",
        requestOptions
      )
        .then((response) => response.text())
        .then((result) => console.log(result))
        .catch((error) => console.log("error", error));
    },
    create() {
      const order = {
        photos: this.imagesUrl,
        date: `${new Date()}`,
        email: this.email,
        status: "pending",
        type: this.$route.query.orderType ?? "",
      };
      store
        .collection(`orders`)
        .add(order)
        .then((response) => {
          console.log(response);
        })
        .catch((err) => {
          console.log(err);
        });

      this.postmail();
      this.formEmpty = false;
    },
    onUpload(image) {
      const storageRef = storage
        .ref(`orders/${this.email}/${image.name}`)
        .put(image);
      storageRef.on(
        `state_changed`,
        (snapshot) => {
          this.uploadValue =
            (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        },
        (error) => {
          console.log(error.message);
        },
        () => {
          this.uploadValue = 100;
          storageRef.snapshot.ref.getDownloadURL().then((url) => {
            this.imagesUrl.push(url);
          });
        }
      );
    },
    onFileChange(e) {
      const files = e.target.files;
      if (!files.length) return;
      this.images = Array.from(files);

      this.images.forEach((image) => this.onUpload(image));
    },
  },
};
</script>

